@import url(https://fonts.googleapis.com/css?family=Barlow|Tomorrow:400,700&display=swap);
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#tooltip {
  position: fixed;
  left: 0;
  top: 0;
  min-width: 100px;
  text-align: center;
  padding: 5px 12px;
  font-family: monospace;
  background: #a0c020;
  display: none;
  opacity: 0;
  border: 1px solid black;
  box-shadow: 2px 2px 3px rgba(0, 0, 0, 0.5);
  transition: opacity 0.25s linear;
  border-radius: 3px;
}
:root {
    --yellow-color: #fcee09; 
    --red-color: #ff003c;
    --black-color: #050a0e;
    --blue-color: #00f0ff;
  }

.btn {
    display: flex;
    align-items: center;
    justify-content: center;
    border: 2px solid #fcee09;
    border: 2px solid var(--yellow-color);
    outline: none;
    background-color: #fcee09;
    background-color: var(--yellow-color);
    color: #050a0e;
    color: var(--black-color);
    cursor: pointer;
    padding: 10px 15px;
    font-family: Tomorrow, sans-serif;
    font-size: .75rem;
    position: absolute;
    z-index: 99;
    left: 3%;
    top: 3%;
    border-radius: 2px;
}

.credits {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid #fcee09;
  border: 2px solid var(--yellow-color);
  opacity: 0.75;
  outline: none;
  background-color: #fcee09;
  background-color: var(--yellow-color);
  color: #050a0e;
  color: var(--black-color);
  cursor: pointer;
  padding: 5px 1px;
  font-family: Tomorrow, sans-serif;
  font-size: .75rem;
  position: absolute;
  z-index: 99;
  right:    0;
  bottom:   0;
  border-radius: 2px;
}


.loading {
    display: inline-block;
    overflow: scroll;
    text-align: left;
    opacity: 0.75;
    width: 500px;
    height: 400px;
    border: 2px solid #fcee09;
    border: 2px solid var(--yellow-color);
    outline: none;
    background-color: #fcee09;
    background-color: var(--yellow-color);
    color: #050a0e;
    color: var(--black-color);
    cursor: pointer;
    padding: 10px 15px;
    font-family: Tomorrow, sans-serif;
    font-size: .75rem;
    position: absolute;
    z-index: 99;
    left: 3%;
    top: 10%;
    border-radius: 2px;
    white-space: normal;
}

